
/* ----- Barra de navegación -----*/

.contenedor_de_opciones {
    display: flex;
    justify-content: space-between;
   
}

.opciones-izquierda{
    position: left;
    margin-top: -10px;
    margin-bottom: -10px;
}

.opciones-izquierda,
.opciones-derecha { 
    list-style: none;
    display: flex;
    align-items: center;
}


.opciones-derecha li {
  margin-right: 20px; /* Añade espacio entre los elementos de la lista */
}

.opciones-izquierda li a,
.opciones-derecha li a {
  text-decoration: none; /* Quita el subrayado del enlace */
  color: black;
  font-weight: 700;
  transition: color 0.3s; /* Transición suave para el cambio de color del texto */
  padding: 10px;
}


/* Estilos para el contenedor de cada opción en hover */
.opciones-izquierda li:not(:first-child):hover,
.opciones-derecha li:hover {
  background-color: #00909e; /* Cambia el color de fondo al pasar el cursor */
  color: white; /* Cambia el color del texto al pasar el cursor */
  cursor: pointer; /* Cambia el cursor al pasar el cursor sobre el contenedor */
  padding: 5px;
  border-radius: 19px;
}

/* ----- logo view ----- */

.home-logo {
    font-size: 0; /* Hace que el texto sea invisible */
    width: auto; /* Establece el ancho automático para ajustarse al tamaño del contenido */
    width: 10px; /* Ajusta el ancho del logo */
}
.home-logo img {
    width: 80px; /* Ajusta el ancho del logo */
}

.opciones-izquierda li:first-child {
    margin-left: -10%; /* Establece un margen derecho automático para mover el primer elemento hacia la izquierda */
  }
/* ----- END desktop view ----- */



/*Para el ocultar este menu cuando este en modo teléfono*/
  @media only screen and (max-width: 768px) {
    .contenedor_de_opciones {
      display: none;
    
  }}
